  async componentWillMount () {
    var _this = this;

    var tipos = await feathers.service ('types').find ({});

    tipos = tipos.data.map (tipo => {
      return {
        value: tipo._id,
        label: tipo.nombre,
      };
    });

    var dds = await feathers.service ('distribution-division').find ({});

    dds = dds.data.map (dd => {
      return {
        value: dd._id,
        label: dd.distributionDivision,
      };
    });

    var empresas = await feathers.service ('clients').find ({});

    empresas = empresas.data.map (empresa => {
      return {
        value: empresa._id,
        label: empresa.nombre,
      };
    });

    if (this.state.id != 'form') {
      var findClient = await feathers.service ('subsidiary').find ({
        query: {
          _id: _this.state.id,
        },
      });
      console.log ('findClient', findClient);

      this.setState ({
        formData: {
          empresa: findClient.data[0].empresa,
          nombre: findClient.data[0].nombre,
          numeroServicio: findClient.data[0].numeroServicio,
          cucc: findClient.data[0].cucc,
          telefono: findClient.data[0].telefono,
          email: findClient.data[0].email,
          tipoUsuario: findClient.data[0].tipoUsuario,
          tipoTarifa: findClient.data[0].tipoTarifa,
          zonaCarga: findClient.data[0].zonaCarga,
          zonaDistribucion: findClient.data[0].zonaDistribucion,
          energia: findClient.data[0].energia,
          potencia: findClient.data[0].potencia,
          cels: findClient.data[0].cels,
          scxsmem: findClient.data[0].scxsmem,
          sum: findClient.data[0].sum,
          iva: findClient.data[0].iva,
          datosTecnicos: findClient.data[0].datosTecnicos,
          uso: findClient.data[0].uso,
          direccionFactura: {
            rfc: findClient.data[0].direccionFactura.rfc,
            calle: findClient.data[0].direccionFactura.calle,
            numeroInterior: findClient.data[0].direccionFactura.calle,
            numeroExterior: findClient.data[0].direccionFactura.calle,
            colonia: findClient.data[0].direccionFactura.colonia,
            cp: findClient.data[0].direccionFactura.cp,
            ciudad: findClient.data[0].direccionFactura.ciudad,
            estado: findClient.data[0].direccionFactura.estado,
            pais: findClient.data[0].direccionFactura.pais,
          },
          contactos: findClient.data[0].contactos,
          medidores: findClient.data[0].medidores,
          cfe: findClient.data[0].cfe,
          cargaConectada: findClient.data[0].cargaConectada,
          demandaContratada: findClient.data[0].demandaContratada,
          nivelTension: findClient.data[0].nivelTension,
          tipo: findClient.data[0].tipo,
          nombreContacto: findClient.data[0].nombreContacto,
          telefonoContacto: findClient.data[0].telefonoContacto,
          emailContacto: findClient.data[0].emailContacto,
        },
        tipoUsuarios: tipos,
        zonaDistribucion: dds,
        empresas: empresas,
      });
    } else {
      this.setState ({
        tipoUsuarios: tipos,
        zonaDistribucion: dds,
        empresas: empresas,
      });
    }
  }

  getOptionsSelect = async (name, url, optionCreateFunction) => {
    // var response = await axios.get(url);
    var response = await feathers.service (url).find ({});

    var options = [];
    response.data.map (option => {
      options.push (optionCreateFunction (option));
    });
    this.setState ({
      optionsSelect: {
        ...this.state.optionsSelect,
        [name]: options,
      },
    });
  };

  onChange = (path, value) => {
    var newFormData = immutable.set (this.state.formData, path, value);

    this.setState ({
      formData: newFormData,
    });
  };

  onAdd = async formData => {
    let _this = this;

    if (this.form.reportValidity ()) {
      if (this.state.id == 'form') {
        let subsidiaryResult = await feathers
          .service ('subsidiary')
          .create (_this.state.formData);
        if (subsidiaryResult._id != undefined) {
          window.location.href = '/#/centros-carga';
        }
      } else {
        let subsidiaryResult = await feathers
          .service ('subsidiary')
          .patch (_this.state.id, _this.state.formData);
        if (subsidiaryResult._id != undefined) {
          window.location.href = '/#/centros-carga';
        }
      }
    }
  };

  toggle (tabPane, tab) {
    const newArray = this.state.activeTab.slice ();
    newArray[tabPane] = tab;
    this.setState ({
      activeTab: newArray,
    });
  }

